import React,{ useEffect , useState} from 'react';
import Editor from "./Editor";
import useLocalStorage from "./useLocalStorage";
import { BrowserRouter as Router, Route ,Switch , Link } from "react-router-dom";


function Home() {
    var [html, setHtml] = useLocalStorage('html', '')
    var [css, setCss] = useLocalStorage('css', '')
    var [js, setJs] = useLocalStorage('js', '')
    var [srcDoc, setSrcDoc] = useState('')

    
    {/* main page with live view and online editor */}
    
    useEffect(() => {
    const timeout = setTimeout(() => {
        setSrcDoc(`
        <html>
            <body>${html}</body>
            <style>${css}</style>
            <script>${js}</script>
        </html>
        `)
    }, 250)

    return () => clearTimeout(timeout)
    }, [html, css, js])

    {/* used PastebinAPI to save the code */}

    function handleclick(){
        var PastebinAPI = require('pastebin-js');

        var pastebin = new PastebinAPI('');

        pastebin = new PastebinAPI({
                        'api_dev_key' : 'peSit_6JuOWc5B90pZaNPtJILLSQr0zw',
                        'api_user_name' : 'saivineeth18',
                        'api_user_password' : 'Saivineeth181@'
                    });

        pastebin
        .createPaste({
            filename: "index.html",
            text: srcDoc,
            title: "index.html",
            format: "html5",
            privacy: 0,
            
        })
        .then(function (data) {
            
            alert(data);          {/* slink will alert after saved */}
        })
        console.log("saved");
    }


    return (
    <>
    {/* IDE for html css and javascript */}
    <div className="pane top-pane">
        <Editor
        language="xml"
        displayName="HTML"
        value={html}
        onChange={setHtml}
        />
        <Editor
        language="css"
        displayName="CSS"
        value={css}
        onChange={setCss}
        />
        <Editor
        language="javascript"
        displayName="JS"
        value={js}
        onChange={setJs}
        />
    </div>
    <button onClick={handleclick} className=" btn btn-primary mb-3">save</button>
    {/* live view */}
    <div className="pane">
        <iframe
        srcDoc={srcDoc}
        title="output"
        sandbox="allow-scripts"
        frameBorder="0"
        width="100%"
        height="100%"
        />
    </div>
    </>
    );
}

export default Home;










